import {Injectable} from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class CoreUrlService {

  private static _AUTH_URL: string = 'https://protected-tundra-66974.herokuapp.com/';
  private static _AUTH_URL_LOCAL: string = 'http://localhost:8000/';
  private _BASE_URL_LOCAL = 'http://localhost:8000/api/v1';
  private _BASE_URL = 'https://protected-tundra-66974.herokuapp.com/api/v1';

  private local: boolean = true;
  private static local: boolean = true;

  constructor() {
  }

  get BASE_URL(): string {
    return this.local ? this._BASE_URL_LOCAL : this._BASE_URL;
  }

  static AUTH_URL() {
    return CoreUrlService.local ? CoreUrlService._AUTH_URL_LOCAL : CoreUrlService._AUTH_URL;
  }
}
