@Injectable({
  providedIn: 'root',
})
export class AllApiService {

  constructor() {
  }
}

@Injectable({
  providedIn: 'root',
})
export class StudentsApiService extends ApiService<Student> {
  getCollectionEndpointExtension(): string {
    return 'students';
  }

  getResourceEndpointExtension(): string {
    return 'student';
  }

  createUserForStudent(user: User, studentID: number): Observable<Student> {
    return this.http.post<Student>(this.getResourceEndPoint() + '/create-user/' + studentID, user);
  }

  getMe() {
    return this.http.get<Student>(this.getResourceEndPoint() + '/me');
  }
}

@Injectable({
  providedIn: 'root',
})
export class CoursesApiService extends ApiService<Course> {
  getCollectionEndpointExtension(): string {
    return 'courses';
  }

  getResourceEndpointExtension(): string {
    return 'course';
  }

  getMyCoursesByTeacher(): Observable<Course[]> {
    return this.http.get<Course[]>(this.getCollectionEndPoint() + '/my-courses/teacher');
  }

  getMyCoursesByStudent(): Observable<Course[]> {
    return this.http.get<Course[]>(this.getCollectionEndPoint() + '/my-courses/student');
  }

  getMyCoursesByTeacherId(teacherId: number): Observable<Course[]> {
    return this.http.get<Course[]>(this.getCollectionEndPoint() + '/by-teacher-id/' + teacherId);
  }

  getAvailableCourses(studentID: number): Observable<Course[]> {
    return this.http.get<Course[]>(this.getCollectionEndPoint() + '/only-available/' + studentID);
  }
}

