import {Injectable} from '@angular/core';
import {Observable} from 'rxjs';
import {HttpClient} from '@angular/common/http';
import {Entity} from '../../model/abstract-entity';
import {BaseUrlService} from '../../../@stacx/services/api-services/base-url.service';

@Injectable({
  providedIn: 'root',
})
export abstract class ApiService<E extends Entity> {

  protected constructor(protected baseUrlService: BaseUrlService,
                        protected http: HttpClient) {
  }

  getCollectionEndPoint(): string {
    return `${this.baseUrlService.BASE_URL}/${this.getCollectionEndpointExtension()}`;
  }

  getResourceEndPoint(): string {
    return `${this.baseUrlService.BASE_URL}/${this.getResourceEndpointExtension()}`;
  }

  abstract getCollectionEndpointExtension(): string;

  abstract getResourceEndpointExtension(): string;


  getAll(): Observable<E[]> {
    return this.http.get<E[]>(this.getCollectionEndPoint());
  }

  getById(id: number): Observable<E> {
    return this.http.get<E>(this.getCollectionEndPoint() + '/' + id);
  }

  post(entity: E): Observable<E> {
    return this.http.post<E>(this.getCollectionEndPoint(), entity);
  }

  delete(id: number): Observable<any> {
    return this.http.delete(this.getResourceEndPoint() + '/' + id);
  }

  put(entity: E): Observable<E> {
    return this.http.put<E>(this.getCollectionEndPoint() + '/' + entity.id, entity);
  }
}
