import {Component, OnInit} from '@angular/core';
import {
  StudentApplicationApiService,
} from '../../@core/service/api-service/implementations/all-api.service';
import {StudentApplication} from '../../@core/model/core/interfaces';
import {NbToastrService} from '@nebular/theme';

@Component({
  selector: 'stacx-apply-student',
  templateUrl: './apply-student.component.html',
  styleUrls: ['./apply-student.component.scss'],
})
export class ApplyStudentComponent implements OnInit {

  application: StudentApplication = {
    address: '',
    birthDay: '',
    email: '',
    gender: '',
    id: null,
    name: '',
    nic: '',
    phone: '',
    school: '',
    state: '',
  };

  constructor(private studentApplicationApiService: StudentApplicationApiService,
              private toastrService: NbToastrService) {
  }

  ngOnInit() {
  }

  submitStudentApplication() {
    this.studentApplicationApiService.post(this.application).subscribe(
      res => {
        this.toastrService.success('Applied Successfully');
      },
      err => {
        this.toastrService.danger('Failed');
      },
    );
  }

}
