import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root',
})
export class BaseUrlService {
  private static _AUTH_URL: string = '';
  private static _AUTH_URL_LOCAL: string = 'http://localhost:8000/';
  private _BASE_URL_LOCAL = 'http://localhost:8000/api/v1';
  private _BASE_URL = '';

  private local: boolean = true;
  private static local: boolean = true;

  constructor() {
  }

  get BASE_URL(): string {
    return this.local ? this._BASE_URL_LOCAL : this._BASE_URL;
  }

  static AUTH_URL() {
    return BaseUrlService.local ? BaseUrlService._AUTH_URL_LOCAL : BaseUrlService._AUTH_URL;
  }
}
